___CSS_LOADER_EXPORT___.push([module.id, `.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#container-home {
    display: flex;
    flex-direction: column;
    width: 100%;
    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;
}

#form-search {
    margin-top: 50px;
    width: 650px;
}

#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 900px;
}

#home h1 {
    text-align:center; 
    font-size: 60px; 
    margin-top: 100px; 
    color: white
}

#home p {
    text-align:center; 
    font-size: 20px; 
    color: white;
}

.barra-pesquisa {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  border-radius: 100px;
  padding: 0px 15px;
  box-shadow: 0 3px 8px rgba(0,0,0,0.2);
}

.barra-pesquisa input {
  border: none;
  outline: none;
  flex: 1;
  font-size: 16px;
  padding: 10px;
  border-radius: 25px;
  color: #333;
}

.barra-pesquisa button {
  background: none;
  border: none;
  cursor: pointer;
  color: #32BBFF;
  font-size: 18px;
}

.barra-pesquisa button:hover {
  color: #1c89c9;
}

.catalogo-container {
  padding: 40px 5%;
  background: linear-gradient(#f2f2f2, #e4e4e4);
  min-height: 100vh;
}

.barra-pesquisa-catalogo {
  width: 100%;
  max-width: 400px;
  margin: 0 auto 40px auto;
  display: flex;
  justify-content: start;
  align-items: center;
  background-color: white;
  border: 2px solid #32bbff;
  border-radius: 25px;
  padding: 10px 15px;
}

.barra-pesquisa-catalogo input {
  border: none;
  outline: none;
  width: 100%;
  font-size: 16px;
  background-color: transparent;
}

.barra-pesquisa-catalogo i {
  color: #32bbff;
  font-size: 18px;
}

.secao-catalogo {
  margin-bottom: 40px;
}

.secao-catalogo h2 {
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.scroll-horizontal {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  padding-bottom: 10px;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
}

.scroll-horizontal::-webkit-scrollbar {
  height: 6px;
}

.scroll-horizontal::-webkit-scrollbar-thumb {
  background: #aaa;
  border-radius: 10px;
}

.item-catalogo {
  flex: 0 0 auto;
  width: 200px;
  background-color: #ccc;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  scroll-snap-align: start;
  transition: transform 0.2s;
}

.item-catalogo:hover {
  transform: scale(1.05);
  cursor: pointer;
}


.imagem {
  width: 100%;
  aspect-ratio: 3 / 4;
  background: #999;
  border-radius: 8px;
  margin-bottom: 10px;
}


@media (max-width: 700px) {
    #home {
        height: 700px;
    }

    #home h1 {
        font-size: 50px;
    }
}

@media (max-width: 676px) {
  .barra-pesquisa {
    width: 100%;
  }

  #form-search {
    width: 300px;
  }
}

@media (max-width: 572px) {

    #home h1 {
        font-size: 40px;
    }
}`, "",{"version":3,"sources":["webpack://./frontend/assets/css/styleLogado.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,yDAA6C;IAC7C,4BAA4B;IAC5B,sBAAsB;IACtB,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,2CAA2C;IAC3C,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,iBAAiB;IACjB;AACJ;;AAEA;IACI,iBAAiB;IACjB,eAAe;IACf,YAAY;AAChB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,uBAAuB;EACvB,oBAAoB;EACpB,iBAAiB;EACjB,qCAAqC;AACvC;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,OAAO;EACP,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,WAAW;AACb;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gBAAgB;EAChB,6CAA6C;EAC7C,iBAAiB;AACnB;;AAEA;EACE,WAAW;EACX,gBAAgB;EAChB,wBAAwB;EACxB,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;EACzB,mBAAmB;EACnB,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,WAAW;EACX,eAAe;EACf,6BAA6B;AAC/B;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,gBAAgB;EAChB,oBAAoB;EACpB,6BAA6B;EAC7B,iCAAiC;AACnC;;AAEA;EACE,WAAW;AACb;;AAEA;EACE,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,sBAAsB;EACtB,mBAAmB;EACnB,aAAa;EACb,kBAAkB;EAClB,wBAAwB;EACxB,0BAA0B;AAC5B;;AAEA;EACE,sBAAsB;EACtB,eAAe;AACjB;;;AAGA;EACE,WAAW;EACX,mBAAmB;EACnB,gBAAgB;EAChB,kBAAkB;EAClB,mBAAmB;AACrB;;;AAGA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA;EACE;IACE,WAAW;EACb;;EAEA;IACE,YAAY;EACd;AACF;;AAEA;;IAEI;QACI,eAAe;IACnB;AACJ","sourcesContent":[".container {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    background-image: url(\"../Images/Vector.png\");\r\n    background-repeat: no-repeat;\r\n    background-size: cover;\r\n    background-position: center;\r\n}\r\n\r\n#container-home {\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: 100%;\r\n    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);\